header {
     width: 100%;
     height: 70px;
     line-height: 70px;
}


.nav__wrapper {
     display: flex;
     align-items: center;
     justify-content: space-between;
}

.logo {
     display: flex;
     align-items: center;
     column-gap: 8px;
}

.logo img {
     width: 1.5rem;
     height: 1.5rem;
}


.logo h1 {
     font-size: 1.2rem;
     font-weight: 700;
     color: var(--primary-color);
}

.menu {
     display: flex;
     align-items: center;
     column-gap: 2.7rem;
     margin-bottom: 0;
}

.nav__item a {
     color: var(--primary-color);
     font-weight: 500;
     cursor: pointer;
}


.nav__icons {
     display: flex;
     align-items: center;
     column-gap: 1.2rem;
}

.nav__icons .profile svg {
     font-size: 1.4rem;
     color: var(--primary-color);
     cursor: pointer;
     border-radius: 50%;
}

.nav__icons .profile {
     position: relative;
     z-index: 9;
}

.nav__icons .profile .profile__actions{
     position: absolute;
     top: 100%;
     left: 0;
     width: 150px;
     z-index: 10;
     padding: 15px;
     display: flex;
     align-items: center;
     justify-content: center;
     flex-direction: column;
     background-color: var(--card-bg-01);
     line-height: 30px;
     display: none;
     cursor: pointer;
}

.show___profileActions{
     display: block;
}

.cart__icon ,  .fav__icon {
     position: relative;
}


.badge{
     position: absolute;
     top: 50%;
     left: 0;
     content: '';
     width: 15px;
     height: 15px;
     background: var(--primary-color);
     color: #fff;
     border-radius: 50px;
     font-size: .7rem;
     display: flex;
     align-items: center;
     justify-content: center;
     font-weight: 600;
     z-index: 10;
}

.nav__icons span img{
     width: 30px;
     height: 30px;
     cursor: pointer;

}

.mobile__menu{
     font-size: 1.3rem;
     color: var(--primary-color);
     display: none;

}

.nav__active {
     font-weight: 600 !important;
}

.sticky__header{
     width: 100%;
     height: 70px;
     line-height: 70px;
     position: sticky;
     top: 0;
     left: 0;
     z-index: 999;
     box-shadow: 3px 3px 8px -3px #ddd;
     background: #fff;
}

@media only screen and (max-width:768px){
     .navigation {
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(0, 0, 0, 0.52);
          z-index: 999;
          display: none;
     }

     .menu{
          position: absolute;
          top: 0;
          right: 0;
          width: 250px;
          height: 100%;
          z-index: 9999;
          background: #fff;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          

     }

     .active__menu{
          display: block;
          
     }
     .mobile__menu{
          display: block;

     }


     .logo img {
          width: 1rem;
          height: 1rem;
     }
     .logo h1 {
          font-size: 1rem;
     }

     .nav__icons span img {
          width: 1.4rem;
          height: 1.4rem;
     }

     .header {
          width: 100%;
          height: 60px;
     }

     
}

